import React from "react";

import "./bootstrap.min.css";

export default class IndexTable extends React.Component {
    constructor(props) {
        super(props);
        this.state = {};
    }

    render() {
        return (
            <div class="container mt-2">
                <div className="text-center text-dark">So Far Implemented</div>
                <table className="table table-striped table-dark mt-2">
                    <thead>
                        <tr>
                            <th scope="col">Latest Visualiser</th>
                            <th scope="col">Category</th>
                            <th scope="col">URL</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr>
                            <td>Rat In A Maze</td>
                            <td>Backtracking</td>
                            <td>
                                <code>/rat-in-a-maze</code>
                            </td>
                        </tr>
                        <tr>
                            <td>Sorting Visualiser</td>
                            <td>Sorting</td>
                            <td>
                                <code>/sorting</code>
                            </td>
                        </tr>
                        <tr>
                            <td>N Queens Problem</td>
                            <td>Backtracking</td>
                            <td>
                                <code>/n-queens-problem</code>
                            </td>
                        </tr>
                        <tr>
                            <td>Binary Search</td>
                            <td>Searching</td>
                            <td>
                                <code>/binary-search</code>
                            </td>
                        </tr>
                        <tr>
                            <td>Linear Search</td>
                            <td>Searching</td>
                            <td>
                                <code>/linear-search</code>
                            </td>
                        </tr>
                    </tbody>
                </table>
                <div className="text-center text-muted">
                    Happy Visualisation Day!
                </div>
            </div>
        );
    }
}
